  border: 2rpx solid #e8eaec;
  background: #fff;

  .gurudin-poster {
    background: #dcdee2;
  }

  .gurudin-content {
    .title {
      color: #2C405A;

      .time {
        color: #808695;
      }
    }

    .author {
      color: #808695;
    }
  }
}

.dark {
  border: 2rpx solid #17233d;
  background: #515a6e;

  .gurudin-poster {
    background: #17233d;
  }

  .gurudin-content {
    .title {
      color: #FFFFFF;

      .time {
        color: #c5c8ce;
      }
    }

    .author {
      color: #c5c8ce;
    }
  }
}

