  position: relative;
  height: 130rpx;
  border-radius: 8rpx;
  clear: both;

  .gurudin-poster, .gurudin-content {
    float: left;
  }

  .gurudin-poster {
    position: relative;
    width: 130rpx;
    height: 126rpx;
    margin-top: 1rpx;
    margin-left: 1rpx;
    border-top-left-radius: 8rpx;
    border-bottom-left-radius: 8rpx;

    image {
      width: 110rpx;
      height: 110rpx;
      margin: 10rpx;
      opacity: 0.8;
      border-radius: 100%;
    }

    .tools {
      position: absolute;
      text-align: center;
      top: 0;
      left: 0;
      height: 130rpx;
      width: 130rpx;

      image {
        width: 50rpx;
