        margin-top: calc((100% - 50rpx) / 2);
      }
    }
  }

  .gurudin-content {
    width: calc(100% - 130rpx - 62rpx);
    height: calc(130rpx - 60rpx);
    padding: 30rpx;

    .title {
      position: relative;
      font-size: 28rpx;
      height: 40rpx;

      .time {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 24rpx;
      }
    }

    .author {
      margin-top: 10rpx;
      font-size: 24rpx;
    }
  }
}

@keyframes mymove
{
	from {
    opacity: 0;
  }

	to {
    opacity: 1;
  }
}

// 旋转动画
.musicRotate{
  animation: rotate 10s linear infinite;
}
  
@keyframes rotate{
  from{
    transform: rotate(0deg)
  }
  to{
    transform: rotate(359deg)
  }
}
  
.musicPaused{
  animation-play-state: paused;
}
</style>
